import React from 'react';
import anime from 'animejs'

import Nav from './components/nav';
import Home from './pages/home';
import Intro from './pages/intro';
import doggy from './static/doggy.png'

import './common.css';

const App = () => (
  <div className="App">
    <div className="App-header">
      <div className="layout">
        <Intro />
        <Home />
        <Nav />
        <img
          className='doggy'
          src={doggy}
          alt='marvin'
          ref={(ref) => {
            anime({
              targets: ref,
              delay: 3000,
              opacity: 0.3,
              duration: 1000,
              easing: 'easeInOutSine'
            })
          }} />
      </div>
    </div>
  </div>
);

export default App;
